/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
   display: block;
}

ol,
ul,
li {
   display: block;
}

a {
   text-decoration: none;
   color: inherit;
}

audio,
canvas,
video {
   display: inline-block;
   *display: inline;
   *zoom: 1;
   max-width: 100%;
}

audio:not([controls]) {
   display: none;
   height: 0;
}

legend {
   border: 0;
   padding: 0;
   white-space: normal;
}

button,
html input[type="button"],
input[type="reset"],
input[type="submit"] {
   -webkit-appearance: button;
   cursor: pointer;
   *overflow: visible;
}

button[disabled],
html input[disabled] {
   cursor: default;
}

table {
   border-collapse: collapse;
   border-spacing: 0;
}

img {
   -o-object-fit: cover;
   object-fit: cover;
   display: block;
}

fieldset {
   border: 1px solid #ccc;
   margin: 0;
   padding: 0;
}

textarea {
   resize: vertical;
   overflow: auto;
   vertical-align: top;
}

button,
input,
textarea,
input,
select {
   display: block;
   border: none;
   background: none;
   font-family: inherit;
   color: inherit;
}

html {
   scroll-behavior: smooth;
}

html,body{
   box-sizing: border-box;
   height: 100%;
   width: 100%;
   font-size: 100%;
   line-height: 1;
   -ms-text-size-adjust: 100%;
   -moz-text-size-adjust: 100%;
   -webkit-text-size-adjust: 100%;
}

// 3 точки в конце текста
.three-dot {
   text-overflow: ellipsis;
   overflow: hidden;
   white-space: nowrap;
}

.hidden {
   position: absolute;
   width: 1px;
   height: 1px;
   margin: -1px;
   border: 0;
   padding: 0;
   clip: rect(0 0 0 0);
   overflow: hidden;
}